@import url(https://fonts.googleapis.com/css?family=Shadows+Into+Light);
:root
{
  /* --yellow: #F5C32C;
    --orange : #FCA61F; */
    --blue: #2c8af5;
    --violet : #3c1ffc;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
} 

/* .font-link {
  font-family: 'Shadows Into Light', cursive;
}  */

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: #242D49;
    color: var(--black);    
}

/* {
  font-family: 'Shadows Into Light', cursive;
      /* font-weight: normal; */
      /* font-size: 25px;  */
    /* } */ 
    
body{
      margin: 0;
    }

.button{
  border-radius: 20px;
  border:none;
  color: white;
  font-size: 20px;
  padding: 10px 26px 11px 26px;
  background: linear-gradient(180deg, #2c8af5 26.71%, #3c1ffc 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
  height: 5rem;
}
.nav-wrapper{
    display: flex;
    justify-content: space-between;
    /* font-family: 'Josefin', cursive; */
      /* font-weight: normal; */
      font-size: 25px; 
}

.home-icon{
    -webkit-transform: scale(2);
            transform: scale(2);
}

.nav-left{
    height: 16vh;
    flex:4 1;
    align-items: left;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    flex-direction: column;

}

.nav-name{
    
    font-size: 40px;
    font-weight: bold;
    color: var(--violet);
    /* text-shadow: 30px 23px 5px rgba(251, 161, 40, 0.42); */
}

.nav-right{
    height: 16vh;
    display: flex;
    flex:3 1;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    flex-direction: column;

}

.nav-list{
    display: flex;
    flex: 10 1;
}

.nav-list>ul{
    list-style: none;
    display: flex;
    grid-gap:2rem;
    gap:2rem;
    margin-right: 2rem;
}

.nav-list>ul>li:hover{
    color: var(--violet);
}

.nav-list>ul>li{
    text-decoration: none;
}

.nav-button {
  flex: 1 1;
  font-size: 25px;
  
}

.nav-button:hover{
  background: white;
  color: var(--violet);
  font-size: 30px;
  font-weight: bold;
  border: 1px solid var(--violet);
}
.intro {
  display: flex;
  height: 77vh;
  margin-top: 6rem;
  font-family: "Shadows Into Light", cursive;
  /* font-weight: normal; */
  font-size: 25px;
}
.intro-left {
  display: flex;
  position: relative;
  flex: 1 1;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  margin-right: 7rem;
}

.intro-name {
  display: flex;
  flex-direction: column;
}

.intro-name > :nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 3rem;
}

.intro-name > :nth-child(2) {
  color: var(--violet);
  font-weight: bold;
  font-size: 4rem;
}

.intro-name > :nth-child(3) {
  font-weight: 100;
  font-size: 35px;
  color: var(--gray);
  margin-top: 10px;
}

.intro-icons {
  margin-top: 2rem;
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.intro-right {
  flex: 1 1;
  position: relative;
}

.intro-right > * {
  position: absolute;
  z-index: 1;
}

.intro-pic {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  margin-top: 19px;
  left: -10%;
  object-fit: scale-down;
}

.blue-blur {
  position: absolute;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #9496f7;
  -webkit-filter: blur(212px);
          filter: blur(212px);
  z-index: -9;
  top: -18%;
  left: 56%;
  -webkit-filter: blur(72px);
          filter: blur(72px);
}
.orange-blur {
  position: absolute;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: rgba(252, 166, 31, 0.45);
  z-index: -9;
  top: 17rem;
  width: 21rem;
  height: 11rem;
  left: -9rem;
  -webkit-filter: blur(72px);
          filter: blur(72px);
}

@media screen and (max-width: 480px) {
  .intro {
    flex-direction: column;
    grid-gap: 7rem;
    gap: 7rem;
    height: 64rem;
  }

  .intro-right {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    left: -3rem;
  }
  .blue-blur {
    display: none;
  }

  .orange-blur {
    display: none;
  }

  .intro img {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }
}

.projects{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 225vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin: bottom 13rem; 
}

.awesome{
    display: flex;
    flex-direction: column;
    position: relative;
}

.awesome>:nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(2) {
  color: var(--violet);
  font-size: 7rem;
  font-weight: bold;
}
.cards {
  position: relative;
}
.cards > * {
  position: absolute;
}

/* blur */
.s-blur2 {
  left: 14rem;
  top: 8rem;
}

.projects-blur1 {
  top: 13rem;
  left: -18rem;
}

@media screen and (max-width: 480px) {
  .services {
    margin-top: 0;
    flex-direction: column;
    grid-gap: 5rem;
    gap: 5rem;
    height: 66rem;
    padding: 0;
  }

  .cards {
    display: flex;
    flex-direction: column;
    grid-gap: 17rem;
    gap: 17rem;
  }
  .cards > * {
    position: static;
  }
}
.card {
  /* width: 20rem; */
  width: 30rem;
  height: 39rem;
  position: absolute;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  text-align: center;
  background: rgba(255, 255, 255, 0.26);
  border: 7px solid var(--orangeCard);
  box-shadow: var(--smboxShadow);
  border-radius: 20px;
  padding: 12px 26px 3rem 26px;
}



.card span:nth-of-type(1) {
  color: var(--black);
  font-size: 30px;
}

.card span:nth-of-type(2) {
  color: var(--gray);
  font-size: 20px;
}

.card-button {
  background: lightgray;
  box-shadow: 0px 24px 60px rgba(0, 0, 0, 0.08);
  border-radius: 7px;
  border: none;
  padding: 10px;
  font-size: 16px;
  color: var(--blue);
  font-weight: bold;
}
/* .p-1-card-wrapper{
    left: 29rem;
} */
.contact-form{
    padding: 0 0 3rem;
    display: flex;
    height: 80vh;
    margin-top: 4rem;
      /* scroll */
  padding-top: 3rem;
}

.c-text {
  display: flex;
  flex-direction: column;
  position: relative;
}

.c-text > :nth-child(1) {
  color: var(--black);
  font-size: 3.5rem;
  font-weight: bold;
}
.c-text > :nth-child(2) {
  color: var(--violet);
  font-size: 4rem;
  font-weight: bold;
  padding-right:3rem ;
}

.contact-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1 1;
}

.contact-right>form
{
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
    align-items: center;
    
}

.contact-right .sender{
    width: 25rem;
    height: 4rem;
    padding: 0.3em;
    outline: none;
    border: 2px solid var(--blue);
    border-radius: 8px;
    font-size: 20px;   
}
textarea{
    height: 4rem!important;
}
