    position: absolute;
  width: 37rem;
  height: 18rem;
  border-radius: 50%;
  background: #9496f7;
  -webkit-filter: blur(212px);
          filter: blur(212px);
  z-index: -9;
  top: -5%;
  left: 28%;
  -webkit-filter: blur(72px);
