@media screen and (max-width: 480px) {
    .contact-form{
        padding: 2rem;
        flex-direction: column;
        grid-gap: 5rem;
        gap: 5rem;
        height: 40rem;
    }
    .contact-right .sender{
        width: 16rem;
    }

}   
